export interface linkObj {
    text: string,
    queryString: string,
    target: string,
    linkType: string,
    url: string
}

export interface mapItemProps {
    id: string,
    latitude: string,
    longitude: string,
    description: string,
    title: string,
    type: string,
    primaryCategory: string,
    attributes: any,
    link: linkObj,
    image: any
}

export interface mapProps {
    data: {
        datasource: any,
        apiKey: any
    },
    editMode?: boolean
}

export interface mapControlProps {
    position: keyof typeof google.maps.ControlPosition;
}